/*
	DEPENDENCIES
*/

@import 'variables';


/*
	STYLES
*/

html
{

}


/*
	SECTIONS
*/

section#central
{
	z-index:99;
	padding:8%;
	font-size:0;
	line-height:0;
	position:relative;
	text-align:justify;

	&:after
	{
		width: 100%;
		content: ".";
		visibility: hidden;
		display: inline-block;
	}

	/*
		CENTER VERTICALLY
	*/

	top:50%;
	transform:translateY(-55%);

	.left,
	.right
	{
		width:50%;
		margin:0 auto;
		line-height:1;
		max-width:500px;
		display:inline-block;
		vertical-align: top;
	}

	.left
	{
		padding-right:20px;
	}

	.right
	{
		margin-top:90px;
		padding-left:20px;
	}

	#location-today
	{
		width:100%;
		color:#123e51;
		font-size:48px;
		font-weight:600;
		text-align:center;
	}

	#temperatures
	{
		position:relative;
	}

	#temp
	{
		margin: 30px 0;
		font-size:320px;
		line-height: .7em;
		@include font_number();

		sup
		{
			font-size: 150px;
			font-weight: 300;
			vertical-align: super;
			line-height: 0;
			margin-left: -30px;
		}
	}


	#temp-min,
	#temp-max
	{
		right:0;
		bottom:0;
		@include font();
		font-size: 50px;
		text-align:center;
		position:absolute;

		sup
		{
			font-size:20px;
			vertical-align: super;
		}

		b
		{
			display:block;
			font-size:28px;
		}
	}

	#temp-max
	{
		top:15px;
		bottom:auto;
	}


	#weather
	{
		text-align:left;
		position:relative;

		canvas
		{
			max-width: 50%;
		}

		b
		{
			top:50%;
			right:0;
			max-width: 50%;
			font-size:40px;
			font-weight:bold;
			text-align:right;
			position:absolute;
			text-transform:uppercase;
			transform:translateY(-50%);
		}
	}




	/*
		NEXT DAYS PREVISION
	*/


	#next-days
	{
		font-size:56px;
		font-weight:lighter;

		ul
		{
			margin:0 auto;
			max-width:380px;

			li
			{
				line-height:0;
				margin:20px 0;

				&:after
				{
					width: 100%;
					content: ".";
					visibility: hidden;
					display: inline-block;
				}

				&:first-of-type
				{
					margin-top:0;
				}

				b
				{
					width:33.333333%;
					font-weight:lighter;
					display:inline-block;
					vertical-align:middle;

					& + b
					{
						text-align:center;
					}

					&:last-of-type
					{
						line-height:1;
						padding-right:20px;
						position:relative;
						@include font_number();
						font-weight:bolder;
						text-align:right;
					}
				}

				sup
				{
					top:0;
					right:0;
					font-size: 23px;
					font-weight: 200;
					position:absolute;
				}
			}
		}

		#app-name
		{
			margin:40px 0 10px;
			font-size:34px;
			text-align:center;
		}

		small
		{
			display:block;
			text-align:center;
		}
	}
}





html.bg-night section#central #location-today
{
	color:white;
}









